/* GENERAL FEATURES */
body {
  font-family: 'Roboto', sans-serif;
  font-size: 20px;
  margin: 0px auto;
  padding: 0px 0px 0px 0px;
  line-height: 1.0;
}
/* Form Container */
.contact-form-container { 
  width: 90%;
  max-width: 500px;
  margin: 50px auto;
  padding: 10px 20px 10px 20px;
}
.contact-form-container p {
  margin: 0px auto;
  padding: 20px 20px 20px 20px;
  text-align: center;
  font-weight: 0px;
  font-size: 20px; 
}
/* Inputs & Textarea (FULL NAME AND EMAIL )*/
.contact-form-container input[type="firstname"],
.contact-form-container input[type="email"] {
  width: calc(100% - 20px);
  padding: 20px;
  margin: 5px auto;
  height: 50px;
  width: 100%; 
  color: rgba(0, 0, 0, 0.55);
  font-size: 15px;
  background-color: whitesmoke;
  border: 2px solid #6610f2;
  border-radius: 10px;
  box-sizing: border-box;
  transition: border-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}
.contact-form-container input[type="firstname"]:focus,
.contact-form-container input[type="email"]:focus {
  border: 4px solid whitesmoke;
  box-shadow: 0 0 8px rgba(0, 123, 255, 0.25);
  outline: none;
}
/* Textarea (MESSAGE) */
.contact-form-container textarea {
  margin: 5px auto;
  resize: vertical;
  height: 200px;
  width:  100%;  
}
/* Textarea Placeholder */
.contact-form-container textarea::placeholder {
  margin: 20px auto;
  padding: 0px 0px 0px 0px;
  font-size: 15px;
}
#contact textarea {
  width: 100%;
  font-size: 15px;
  padding: 20px 20px 20px 20px;
  border-radius: 10px;
  outline: none;
  transition: border-color 0.3s ease;
}

#contact textarea.limit-reached {
  border: 4px solid red;
  color: red;
}
#contact .char-count {
  margin: 20px auto;
  font-size: 20px;
  color: whitesmoke;
  text-shadow: 1px 1px 2px black;
}
#contact .char-count.limit-reached {
  color: red;
  text-shadow: 1px 1px 2px black;
}


/*  DARK MODE */


body.dark-mode .contact-form-container input[type="firstname"],
body.dark-mode .contact-form-container input[type="email"]{
  background-color: #333;
  color: goldenrod;
  border: 2px solid white;
}
body.dark-mode #contact textarea {
  background-color: #333;
  color: goldenrod;
  border: 2px solid white;
}
body.dark-mode .contact-form-container input[type="firstname"]:hover,
body.dark-mode .contact-form-container input[type="email"]:hover{
  box-shadow: 0 0 20px goldenrod;
  background-color: #333;
  border: 4px solid goldenrod;
  border-radius: 20px;
}
body.dark-mode #contact textarea:hover {
  box-shadow: 0 0 20px goldenrod;
  background-color: #333;
  border: 4px solid goldenrod;
  border-radius: 20px;
}


/*  LIGHT MODE */


body.light-mode .contact-form-container input[type="firstname"],
body.light-mode .contact-form-container input[type="email"] {
  background-color: whitesmoke;
  color: #6610f2;
  border: 2px solid navy;
}
body.light-mode #contact textarea {
  background-color: whitesmoke;
  color: #6610f2;
  border: 2px solid navy;
}
body.light-mode .contact-form-container input[type="firstname"]:hover,
body.light-mode .contact-form-container input[type="email"]:hover{
  box-shadow: 0 0 20px #6610f2;
  /*background-color: #333;*/
  border: 4px solid #6610f2;
  border-radius: 20px;
}
body.light-mode #contact textarea:hover {
  box-shadow: 0 0 20px #6610f2;
/*background-color: #333;*/
  border: 4px solid #6610f2;
  border-radius: 20px;
}

/* Responsiveness */
@media (max-width: 600px) {
  .contact-form-container {
    padding: 15px;
  }

  .contact-form-container h3 {
    font-size: 1.5rem;
  }

  .contact-form-container button[type="submit"] {
    width: 100%;
    height: 60px;
    padding: 0px 0px 0px 0px;
    margin: 50px 0px 0px 0px;
    background: linear-gradient(135deg, black, #6610f2);
    color: #ffffff;
    text-shadow: 0 2px 0 black;
    border: solid 2px whitesmoke;
    border-radius: 40px;
    font-size: 15px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease-in-out, transform 0.1s;
  }
  .contact-form-container button[type="submit"]:hover {
    background: linear-gradient(135deg, goldenrod, #6610f2);
    border: solid 2px goldenrod;
  }
  .contact-form-container button[type="submit"]:active {
    transform: translateY(2px);
  }
}
